.posts-list {
  //padding-left: calc-percent(80, 1060);
  //padding-right: calc-percent(80, 1060);

  &__item {
    margin-bottom: 60px;
    &:last-child {
      margin-bottom: 0;
    }
  }

  @include bp-medium {
    //&__main-post {
    //  padding-top: 85px;
    //  margin-bottom: 110px;
    //}
  }

  &__profile-social {
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
  }
  &__category {
    font-weight: normal;
    font-size: calc-rem(14);
    line-height: calc-lh(14, 14);
    color: $gray;
    text-transform: uppercase;
    text-align: left;
  }

  &__btn {
    padding: 0;
  }
  &__picture {

    width: 100%;
    height: auto;

    @include transition-all;

    &:hover {
      filter: brightness(1.1);
      transform: scale(1.03);
    }
  }

  &__title {
    position: relative;
    margin-top: 10px;
    margin-bottom: 5px;
    font-size: calc-rem(26);
    line-height: calc-lh(26, 26);
    font-weight: bold;
    color: $gray-dark;
    text-align: left;
    text-transform: uppercase;

    @include bp-medium {
      font-size: calc-rem(16);
      line-height: calc-lh(16, 16);
    }

    &-link  {

      color: $gray-dark;
      @include transition-all;
    }
  }

  &__content {
    margin-bottom: 45px;
    font-size: calc-rem(14);
    line-height: calc-lh(24, 14);
  }
}