.send-message {

  &__form {
    @include bp-medium {
      text-align: left;
    }
    &-textarea {
      width:100%;
      display: block;
      min-height: 145px;
      padding: 10px;
      box-sizing: border-box;
      margin-bottom: 25px;
      border: 1px solid $gray;
      font-size: calc-rem(16);
      line-height: calc-lh(16, 16);
      text-transform: uppercase;
      resize: none;

      &:focus {
        outline: 0;
      }
    }
    &-btn {
       width: 100%;
       padding: 10px;
       box-sizing: border-box;
       font-size: calc-rem(24);
       line-height: calc-lh(30, 24);
       font-weight: bold;
       color: white;
       background-color: $red;
       &:focus {
         outline: 0;
       }
       @include bp-medium {
         width: 200px;
         border-radius: 2px;

       }
    }
    &-input {
       width:100%;
       padding: 10px;
       box-sizing: border-box;
       border: 1px solid $gray;
       text-transform: uppercase;
       margin-bottom: 25px;
       font-size: calc-rem(16);
       line-height: calc-lh(16, 16);
       &:focus {
        outline: 0;
       }
    }
  }


}