.social-links-list {
  margin: 0;
  padding: 0;
  font-size: 0;
  list-style: none;

  &__link {
    display: block;
    padding: 5px;
    vertical-align: top;
    background-color: transparent;
    fill: $white;

    @include transition-all;

    &:hover {
      fill: $red;
    }

    &:active {
      fill: $gray;
    }
    & > svg,
    & span svg {
      display: block;
      width: 20px;
      height: 20px;
    }


    &--profile-social {
      fill: $red;

      @include bp-medium {
        & > svg {
          width: 16px;
          height: 16px;
        }

        & > svg,
        & span svg {
          width: 16px;
          height: 16px;
        }
      }

        &:hover {
          fill: $gray-dark;
        }
        &:active {
          fill: $gray;
        }
    }

    &--footer-social {
      fill: $red;
      &:hover {
        fill: $gray-dark;
      }
      &:active {
        fill: $gray;
      }
    }


    &--contact-link {
      position: relative;
      padding-left: 35px;
      display: inline-block;
      color: $gray;
      font-size: calc-rem(14);
      line-height: calc-lh(32, 14);
      text-transform: none;

      fill: $red;
      &:hover {
        fill: $gray-dark;
      }
      &:active {
        fill: $gray;
      }
      &-label {
        &:hover {
          color: $gray;
          fill: $red ;
        }
        &:active {
          color: $gray;
          fill: $red ;
        }
      }
      & > span {
        display: inline-block;
        vertical-align: text-top;
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);

      }
    }


  }

  &__item {
    display: inline-block;
    vertical-align: middle;

    &--contact-item {
      display: block;
    }
    &:last-of-type {
      margin-right: 0;
    }

    &:last-of-type > .social-links-list__link {
      padding-right: 0;
    }
  }
}

