/*------------------------------------*\
    #GRID
\*------------------------------------*/

// Lost Grid System (ratio, cycle, gutter)
.grid {
  lost-utility: clearfix;
  width: 100%;
  @include bp-zero {
    &__zero-col-1-2 {
      lost-column: (1 / 2) (0) (0);
    }
  }
  @include bp-xsmall {
    &__xsmall-col-1-2 {
      lost-column: (1 / 2) (2) (5.26%);
    }
  }
  @include bp-small {
    &__small-col-1-2 {
      lost-column: (1 / 2) (2) (6.13%);
    }
  }
  @include bp-medium {
    &__medium-col-1 {
      lost-column: 1;
    }

    &__medium-col-1-2 {
      lost-column: (1 / 2) (2) (6.13%);
    }

    &__medium-col-1-3 {
      lost-column: (1 / 3) (3) (2.56%);
    }
    &__medium-col-1-4 {
      lost-column: (1 / 4) (4) (2.56%);
    }

    &__medium-col-2-3 {
      lost-column: (2 / 3) (0) (0);
    }

    &__medium-col-1-6 {
      lost-column: (1 / 6) (0) (0);
    }

    &__medium-col-5-6 {
      lost-column: (5 / 6) (0) (0);
    }

    &__medium-right-move-2-3 {
      lost-move: (2 / 3) (0) (0);
    }

    &__medium-left-move-1-3 {
      lost-move: (-1 / 3) (0) (0);
    }
  }
  @include bp-xlarge {
    &__xlarge-col-1 {
      lost-column: 1;
    }
    &__xlarge-col-1-2 {
      lost-column: (1 / 2) (2) (10.85%);
    }
    &__xlarge-col-1-6 {
      lost-column: (1 / 6) (0) (0);
    }

    &__xlarge-col-2-6 {
      lost-column: (2 / 6) (0) (0);
    }

    &__xlarge-col-3-6 {
      lost-column: (3 / 6) (0) (0);
    }

    &__xlarge-col-4-6 {
      lost-column: (4 / 6) (0) (0);
    }

    &__xlarge-col-5-6 {
      lost-column: (5 / 6) (2) (0);
    }
  }
}
