/*------------------------------------*\
    #BASE - SCAFFOLDING
\*------------------------------------*/
:root {
  font-size: $font-size-browser+px;
  background-color: $body-bg;
}

body > svg {
  display: none;
}

body {
  font-style: normal;
  font-size: calc-rem(16);
  line-height: calc-lh(30, 16);
  font-family: $font-family-base;
  color: $text-color;
  text-transform: capitalize;
}

// Links
a {
  color: $link-color;
  text-decoration: none;

  &:hover {
    color: $link-hover-color;
    text-decoration: none;
  }

  &:active {
    color: $link-active-color;
  }
}

