.index-section {
  padding-top: 60px;
  padding-bottom: 60px;
  text-align: center;
  background-color: $white;

  &__skill-wrapper {
    margin-bottom: 60px;
    &:last-of-type {
      margin-bottom: 0;
    }

    @include bp-small {
      &:nth-of-type(4n+3),
      &:nth-of-type(4n+4) {
        margin-bottom: 0;
      }
    }

    @include bp-medium {
      margin-bottom: 0 !important;
    }
  }


  &:nth-of-type(odd) {
    background-color: $gray-light;
  }

  &__container {
    padding-left: calc-percent(87, 1344);
    padding-right: calc-percent(87, 1344);
    max-width: 1170px;
    margin: 0 auto;

    &--hidden {

    }

  }
  &__contacts {
    margin-bottom: 40px;
    text-align: left;
    @include bp-medium {
      margin-bottom: 0;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    padding: 0;
    color: $gray-dark;
    font-size: calc-rem(24);
    line-height: calc-lh(24, 24);
    text-transform: uppercase;

    &--contact-title {
      text-align: center;
      font-size: calc-rem(18);
      line-height: calc-lh(18, 18);
      @include bp-medium {
        text-align: left;
      }
    }

    &--custom-title {
      @include bp-xlarge {
        text-align: left;
      }

    }
  }
  
  &__skills {
    @include bp-large {
      min-height: 200px;
    }
  }
  &__description {
    margin-bottom: 40px;

    &--custom-desc {
      @include bp-xlarge {
        text-align: justify;
      }

    }
    &:last-child {
      margin-bottom: 0;
    }
  }


  &__picture {
    display: none;
    width: 100%;
    height: auto;
    @include bp-xlarge {
      display: block;
    }
  }





}
