.main-header {
  position: relative;
  height: 250px;
  height: 100vh;
  background-image: url('../pic/bg-header.jpg');
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;

  @include bp-medium {
    height: 400px;
    height: 100vh;
  }

  @include bp-xlarge {
    height: 650px;
    height: 100vh;
  }

  &__content {
    position: absolute;
    top: 50%;
    left: 50%;
    text-align: center;
    transform: translate(-50%, -50%);
  }

  &__social-links {
    position: absolute;
    top: 20px;
    right: calc-percent(87, 1344);

  }

  &__title {
    padding: 0;
    margin: 0;
    margin-bottom: 25px;
    color: $white;
    font-family: $font-family-base;
    font-weight: 400;
    font-size: calc-rem(24);
    line-height: calc-lh(24, 24);
    text-transform: uppercase;
    //letter-spacing: (200 / 1000 + em);

    @include bp-medium {
      margin-bottom: 45px;
      font-size: calc-rem(36);
      line-height: calc-lh(36, 36);
    };



    > span {
      color: $red;
    }
  }
  &__btn {
    padding: 17px 27px;
    border-radius: 1px;
    border: 2px solid $white;
    color: $white;
    font-family: $font-family-base;
    font-size: calc-rem(18);
    line-height: calc-lh(16, 18);
    font-weight: bold;

    &:hover {
      border: 2px solid darken( $white, 20% );
      color: darken( $white, 20% );

    }
    &:active {
      border: 2px solid darken( $white, 40% );
      color: darken( $white, 40% );
    }

    @include bp-medium {
      font-size: calc-rem(24);
      line-height: calc-lh(21, 24);

    }
  }

  @include bp-medium {

    &__container {

      margin: 0 auto;
      max-width: 1170px;
    }
  }
}