.skill {
  margin: 0 auto;
  max-width: 200px;
  position: relative;


  &__progressbar-label {
    font-size: 1.25rem;
    line-height: 1.5;
    font-weight: bold;

    @include bp-large {
      font-size: 2.25rem;

    }
  }

  &__progressbar-custom-text {
    display:block;
    font-size:0.85rem;
    line-height:1;
    color:#919191;
    text-align:center;
    font-weight: normal;

    @include bp-large {
      font-size:1.25rem;

    }
  }
}
